<template>
  <div class="tramites-online">
    <div class="container">
      <div class="card card-muni">
        <div class="ask">
          <div class="col-md-12">
            <h3>El registro se deber hacer considerando si es Persona Natural, Jurdica o Institucin Pblica</h3>
            <p class="text-center desc-ask">Los documentos que emitir la Municipalidad de Miraflores sern conforme al tipo de persona registrado: natural, jurdica o entidad pblica.</p>
          </div>
          <div class="content_card">
            <div class="card" v-if="usuarioMigrado">
              <!-- <a href="javascript:;" @click="ObtenerTipoTramite(nombreTipoTramite,'37,39',2)"> -->
              <router-link to="/registroUsuario/natural">
                <h4>
                  Persona
                  <br />Natural
                </h4>
                <div>
                  <img src="../../assets/personanatural.png" alt="">
                </div>
                <p>Iniciar trmite como <b>persona natural</b></p>
              </router-link>
            </div>
            <div class="card">
              <!-- <a href="javascript:;" @click="ObtenerTipoTramite(nombreTipoTramite,'38',2);"> -->
              <router-link to="/registroUsuario/juridica">
                <h4>
                  Persona
                  <br />Jurdica
                </h4>
                <div>
                  <img src="../../assets/personajuridica.png" alt="">
                </div>
                <p>Registrar trmite en representacin de una <b>persona jurdica</b></p>
              </router-link>
            </div>
            <div class="card">
              <router-link to="/registroUsuario/institucion">
                <h4>
                  Entidad <br /> Pblica
                </h4>
                <div>
                  <img src="../../assets/institucion.png" alt="">
                </div>
                <p>Registrar trmite en representacin de una <b>entidad pblica</b></p>
              </router-link>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<style>
</style>
<script>
import axios from "axios";
import EventBus from "../../helpers/EventBus.js";
import Constantes from "../../store/constantes.js";
import { ModelSelect } from "vue-search-select";
import _ from "lodash";

import Util from "../../tools/util";

export default {
  components: {
    ModelSelect,
  },
  data() {
    return {
      usuarioMigrado: true
    }
  },
  created() {
    let usuarioMigrado = Util.storage.get("usuarioMigrado");
    if (usuarioMigrado != null) {
      console.log(usuarioMigrado);
      this.usuarioMigrado = usuarioMigrado;
    }
  }
}
</script>
<style lang="scss" scoped>
.info-text {
  padding: 30px 10px 0px;
  color: #7d7d7d;
}
.guia {
  padding: 23px 20px;
  border: solid 1px #c4e6ff;
  border-radius: 18px;
  height: 100%;
  background: #e5f4ff;
  color: #0078cf;
  align-items: center;
  display: flex;
  p {
    margin-bottom: 0;
    max-width: 167px;
    margin: auto;
    text-align: center;
  }
}
.card.card-muni {
  padding-bottom: 40px;
  margin-bottom: 60px;
}
.tramites-online {
  .ask {
    padding-bottom: 50px;
    .desc-ask {
      max-width: 500px;
      margin: 30px auto 30px auto;
    }
  }
  h3 {
    padding-top: 30px;
    // padding-bottom: 10px;
  }
  .content_card {
    // max-width: 500px;
    margin: auto;
    display: flex;
    flex-direction: row;
    justify-content: center;
    .card {
      min-width: 230px;
      display: flex;
      align-items: center;
      padding: 15px;
      min-height: 200px;
      margin-left: 15px;
      margin-right: 15px;
      border: 2px solid #ddd;
      border-radius: 10px;
      &:hover {
        border: 2px solid #0078cf;
      }
      h4 {
        font-weight: bold;
        color: #0078cf;
        font-size: 16px;
        margin-bottom: 20px;
      }
      img {
        max-width: 60px;
        margin-bottom: 15px;
      }
      p {
        max-width: 180px;
        margin: auto;
      }
      > * {
        text-align: center;
      }
    }
  }
  .lista {
    margin-left: 0;
    padding-left: 0;
    list-style: none;
    border-radius: 15px;
    border: solid 1px #ddd;
    li {
      border-bottom: solid 1px #ddd;
      // border-radius: 5px;
      padding-top: 10px;
      padding-bottom: 10px;
      padding-left: 15px;
    }
  }
  @media only screen and (max-width: 600px) {
    .btn-mobile .form-group {
      text-align: center !important;
    }
    .card-muni {
      padding-left: 30px;
      padding-right: 30px;
    }
    .content_card {
      display: block;
    }
    .content_card .card {
      margin-bottom: 20px;
    }
  }
}
</style>


